.road-map {
    position: relative;
    padding: 5px 0 30px;
}

.road-map::before {
    display: block;
    content: "";
    position: absolute;
    width: 10px;
    top: 0;
    bottom: 0;
    left: 50%;
    margin-left: -5px;
    background-color: #e8c238;
    border-radius: 5px;
}

.road-map .road-map-item-wp {
    margin-top: 30px;
    margin-bottom: 0;
    width: 50%;
    position: relative;
}

.road-map .road-map-item-wp.even {
    margin-left: 0;
    margin-right: auto;
    padding-right: 30px;
}

.road-map .road-map-item-wp.odd {
    margin-right: 0;
    margin-left: auto;
    padding-left: 30px;
}

.road-map .road-map-item-wp .road-map-item {
    border: 2px solid #e8c238;
    padding: 15px;
    border-radius: 10px;
}

.road-map .road-map-item-wp.active.even .road-map-item {
    border-top-right-radius: 0;
}

.road-map .road-map-item-wp.active.odd .road-map-item {
    border-top-left-radius: 0;
}

.road-map .road-map-item-wp .road-map-item-detail {
    /* color: #c5a42d; */
    color: #000;
}

.road-map .road-map-item-wp.active .road-map-item-detail {
    color: #11269b;
    text-shadow: 1px 1px 1px rgba(0,0,0,.5);
}

.road-map .road-map-item-wp.current .road-map-item-detail {
    font-weight: 600;
}

.road-map .road-map-item-wp.active::before,
.road-map .road-map-item-wp.active::after {
    content: "";
    display: block;
    position: absolute;
}

.road-map .road-map-item-wp.active::before {
    width: 50%;
    top: 0;
    height: 2px;
    background-color: #e8c238;
}

.road-map .road-map-item-wp.even.active::before {
    right: 0;
}

.road-map .road-map-item-wp.odd.active::before {
    left: 0;
}

.road-map .road-map-item-wp.active::after {
    z-index: 2;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: #fff;
    border: 3px solid #e8c238;
    top: -10px;
}

.road-map .road-map-item-wp.active.current::after {
    width: 30px;
    height: 30px;
    top: -15px;
    border-width: 4px;
    background-color: rgb(47, 61, 138);
}

.road-map .road-map-item-wp.active.even::after {
    right: -10px;
}

.road-map .road-map-item-wp.active.current.even::after {
    right: -15px;
}

.road-map .road-map-item-wp.active.odd::after {
    left: -10px;
}

.road-map .road-map-item-wp.active.current.odd::after {
    left: -15px;
}

@media screen and (max-width: 599px) {
    .road-map::before {
        left: 100%;
    }

    .road-map .road-map-item-wp {
        width: 100%;
    }

    .road-map .road-map-item-wp.odd {
        margin-left: 0;
        margin-right: auto;
        padding-left: 0;
        padding-right: 30px;
    }

    .road-map .road-map-item-wp.active.odd::after {
        left: auto;
        right: -10px;
    }
    
    .road-map .road-map-item-wp.active.current.odd::after {
        left: auto;
        right: -15px;
    }
}

